.footer {
    padding: $footer-padding;
    background-color: $footer-bg-color;
    border: solid $border-color;
    margin-bottom: $footer-margin;
}

.footer-item-1 {
    float: left;
}

.footer-item-1, li {
    display: inline;
}

.footer-item-2{
    text-align: right;
    padding-inline-end: $footer-padding-right;
}

.footer, .site-content::after {
    height: $footer-height;
    max-height: $footer-height;
}  
