.header-bar {
    padding: 10px;
    margin-top: 10%;
    background-color: $header-bg-color;
    border: solid $border-color;
}

.header-bar,a {
    color: $text-color;
}

.header-bar,a:link{
    text-decoration: none;
}

.header-title {
    margin-top: $header-margin;
    margin-bottom: $header-margin;
    float: left;
}

.header-nav {
    text-align: right;
    margin-top: $header-margin;
    margin-bottom: $header-margin;
}